table {
  border: none;
  font-size: 12px !important;
  margin: 0;
  padding: 0;
  width: 100%;
}

tr {
  margin: 0;
  padding: 0;
}

td {
  height: 19px;
}

th {
  height: 19px;
}

label {
  background-color: lightgrey;
  display: inline-block;
  width: 100%;
  height: 100%;
}

label.largeSizedLabel {
  width: 700px;
  margin-left: 6px;
}

label.largeSizedLabel input {
  width: 30px;
  float: right;
}

label.mediumSizedLabel {
  width: 320px;
  margin-left: 6px;
}

label.mediumSizedLabel input {
  width: 30px;
  float: right;
}

label.shortLabel {
  width: 160px;
  margin-left: 6px;
  max-height: 18px;
}

label.veryShortLabel input {
  width: 20px;
}

label.veryShortLabel {
  width: 80px;
}

label.shortLabel input {
  width: 40px;
}

td input {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
  border: 0;
  font-style: italic;
  background-color: #ccddff;
}

table.skillTable {
  margin: 0;
  padding: 0;
}

table.skillTable td {
  min-width: 61px;
}

table.skillTable label {
  width: 10 0px;
}

div.characterSheet table tbody td {
  width: 280.8px;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  vertical-align: top;
}

div.characterSheet h4 {
  text-align: center;
}

td h4 {
  height: 20px;
  margin-bottom: 0;
  margin-top: 19px;
}

td h5 {
  height: 19px;
  margin-bottom: 0;
  margin-top: 19px;
}

.smalltd {
  width: 40px !important;
}

.smalltd2 {
  width: 60px !important;
}

.smalltd3 {
  width: 90px !important;
}

.bigtd {
  width: auto !important;
}

table.lifeboxes tbody tr td {
  height: 9px;
  width: 9px;
  border: 1px solid black;
  border-left: none;
  line-height: 0;
  font-size: 0;
  margin: 0;
  padding: 0;
  display: inline-block;
}

table.lifeboxes tbody tr td:first-child {
  border-left: 1px solid black;
}
table.lifeboxes tbody tr td:nth-child(5n + 1) {
  margin-left: 2px;
  border-left: 1px solid black;
}

table.lifeboxes tbody tr {
  line-height: 0;
  font-size: 0;
  height: 0;
  margin: 0;
  padding: 0;
}

span.exhaustionBoxes {
  height: 15px;
  width: 15px;
  display: inline-block;
  border: 1px solid black;
  margin: 0;
  margin-right: 1px;
  margin-top: 7px;
  padding: 0;
}

span.manaBoxes {
  height: 10px;
  width: 10px;
  display: inline-block;
  border: 1px solid black;
  border-left: none;
  margin: 0;
  padding: 0;
  margin-right: 0;
}

span.manaBoxes:nth-child(5n + 1) {
  border-left: 1px solid black;
  margin-left: 2px;
}

span.mentalHealthBoxes {
  height: 12px;
  width: 11px;
  display: inline-block;
  border: 1px solid black;
  margin: 0;
  padding: 0;
  margin-right: 1px;
}

span.mentalHealthBoxes:nth-child(5n + 1) {
  border-left: 1px solid black;
  margin-left: 2px;
}

table.statboxes thead tr th:first-child {
  width: 25%;
}
table.statboxes thead tr th:nth-child(2) {
  width: 57%;
}

table.statboxes thead tr th span.carry {
  float: left;
}

table.statboxes thead tr th span.exhaustion {
  float: right;
}

span.middash {
  float: left;
  text-align: center;
  width: 30%;
}

span.smallLabel {
  width: 27%;
  display: inline-block;
  height: 100%;
}

span.SelectTextOption {
  display: block;
  border: 1px solid gray;
  border-radius: 2px;
  height: 22px;
  margin: 2px;
  padding: 2px;
  cursor: pointer;
  background-color: #ccddff;
  width: 80px;
}

span.SelectTextBlock {
  display: block;
  position: absolute;
}
